  font-family: sans-serif;
  font-size: 14px;
  color: #494949;
  background-color: #F2F6F8;
  border-radius: 1rem;
  padding: 1.4rem 2rem;
  box-sizing: border-box;
}

/*-------------------------
	Input
-------------------------*/
.jFiler-input {
  position: relative;
  display: block;
  width: 100%;
  max-width: 400px;
  height: 35px;
  line-height: 32px;
  margin: 0 0 15px 0;
  background: #fefefe;
  border: 1px solid #cecece;
  font-size: 12px;
  font-family: sans-serif;
  color: #888;
  border-radius: 4px;
  cursor: pointer;
  overflow: hidden;
  -webkit-box-shadow: rgba(0, 0, 0, 0.25) 0 4px 5px -5px inset;
  -moz-box-shadow: rgba(0, 0, 0, 0.25) 0 4px 5px -5px inset;
  box-shadow: rgba(0, 0, 0, 0.25) 0 4px 5px -5px inset;
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding-box;
  background-clip: padding-box;
}

.jFiler-input.focused {
  outline: none;
  -webkit-box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
}

.jFiler.dragged .jFiler-input {
  border: 1px dashed #aaaaaa;
  background: #f9f9f9;
}

.jFiler.dragged .jFiler-input:hover {
  background: #FFF8D0;
}

.jFiler.dragged .jFiler-input * {
  pointer-events: none;
}

.jFiler.dragged .jFiler-input .jFiler-input-caption {
  width: 100%;
  text-align: center;
}

.jFiler.dragged .jFiler-input .jFiler-input-button {
  display: none;
}

.jFiler-input-caption {
  display: block;
  float: left;
  height: 100%;
  padding-left: 10px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.jFiler-input-button {
  position: absolute;
  display: block;
  float: right;
  height: 100%;
  top: 0;
  right: 0;
  padding: 0 15px;
  border-left: 1px solid #ccc;
  color: #666666;
  text-align: center;
  background-color: #eee;
  background-image: -webkit-gradient(linear, 0 0, 0 100%, from(#fff), to(#eee));
  background-image: -webkit-linear-gradient(top, #fff, #eee);
  background-image: -o-linear-gradient(top, #fff, #eee);
  background-image: linear-gradient(to bottom, #fff, #eee);
  background-image: -moz-linear-gradient(top, #fff, #eee);
  -webkit-transition: all 0.1s ease-out;
  -moz-transition: all 0.1s ease-out;
  -o-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.jFiler-input-button:hover {
  -moz-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.07);
  -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.07);
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.07);
}

.jFiler-input-button:active {
  -moz-box-shadow: inset 0 3px 10px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: inset 0 3px 10px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 3px 10px rgba(0, 0, 0, 0.1);
}

/*-------------------------
	File Icons
-------------------------*/
span.jFiler-icon-file {
  position: relative;
  width: 57px;
  height: 70px;
  display: inline-block;
  line-height: 70px;
  text-align: center;
  border-radius: 3px;
  color: #fff;
  font-family: sans-serif;
  font-size: 13px;
  font-weight: bold;
  overflow: hidden;
  box-shadow: 42px -55px 0 0 #A4A7AC inset;
}

span.jFiler-icon-file:after {
  position: absolute;
  top: -1px;
  right: -1px;
  display: inline-block;
  content: "";
  border-style: solid;
  border-width: 16px 0 0 16px;
  border-color: transparent transparent transparent #DADDE1;
}

span.jFiler-icon-file i[class*=icon-jfi-] {
  font-size: 24px;
}

/* image */
span.jFiler-icon-file.f-image {
  box-shadow: 42px -55px 0 0 #e15955 inset;
}

span.jFiler-icon-file.f-image:after {
  border-left-color: #c6393f;
}

/* video */
span.jFiler-icon-file.f-video {
  box-shadow: 42px -55px 0 0 #4183d7 inset;
}

span.jFiler-icon-file.f-video:after {
  border-left-color: #446cb3;
}

/* audio */
span.jFiler-icon-file.f-audio {
  box-shadow: 42px -55px 0 0 #5bab6e inset;
}

span.jFiler-icon-file.f-audio:after {
  border-left-color: #448353;
}

/*-------------------------
	Progress Bar
-------------------------*/
.jFiler-jProgressBar {
  height: 8px;
  background: #f1f1f1;
  margin-top: 3px;
  margin-bottom: 0;
  overflow: hidden;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}

.jFiler-jProgressBar .bar {
  float: left;
  width: 0;
  height: 100%;
  font-size: 12px;
  color: #ffffff;
  text-align: center;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #50A1E9;
  box-sizing: border-box;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  -webkit-transition: width 0.3s ease;
  -moz-transition: width 0.3s ease;
  -o-transition: width 0.3s ease;
  transition: width 0.3s ease;
}

.jFiler-jProgressBar .bar.dark {
  background-color: #555;
}

.jFiler-jProgressBar .bar.blue {
  background-color: #428bca;
}

.jFiler-jProgressBar .bar.green {
  background-color: #5cb85c;
}

.jFiler-jProgressBar .bar.orange {
  background-color: #f7a923;
}

.jFiler-jProgressBar .bar.red {
  background-color: #d9534f;
}

/*-------------------------
	Thumbs
-------------------------*/
.jFiler-row:after {
  display: table;
  line-height: 0;
  content: "";
  clear: both;
}

.jFiler-items ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.jFiler-items ul.list-inline li {
  display: inline-block;
  padding-right: 5px;
  padding-left: 5px;
}

.jFiler-items .pull-left {
  float: left;
}

.jFiler-items .pull-right {
  float: right;
}

.jFiler-item {
  float: left;
}

.jFiler-item .jFiler-item-container {
  position: relative;
  margin: 0 20px 30px 0;
  padding: 10px;
  border: 1px solid #e1e1e1;
  border-radius: 3px;
  background: #fff;
  -webkit-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.06);
  -moz-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.06);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.06);
}

.jFiler-item .jFiler-item-container .jFiler-item-thumb {
  position: relative;
  width: 160px;
  height: 115px;
  min-height: 115px;
  border: 1px solid #e1e1e1;
  overflow: hidden;
}

.jFiler-item .jFiler-item-container .jFiler-item-thumb .jFiler-item-thumb-image {
  width: 100%;
  height: 100%;
  text-align: center;
}

.jFiler-item .jFiler-item-container .jFiler-item-thumb img {
  max-width: none;
  max-height: 100%;
}

.jFiler-item .jFiler-item-container .jFiler-item-thumb span.jFiler-icon-file {
  margin-top: 20px;
}

.jFiler-item-thumb-image.fi-loading {
  background: url("data:image/gif;base64,") no-repeat center;
  width: 100%;
  height: 100%;
}

.jFiler-item .jFiler-item-container .jFiler-item-info {
  position: absolute;
  bottom: -10%;
  left: 0;
  width: 100%;
  color: #fff;
  padding: 6px 10px;
  background: -moz-linear-gradient(bottom, rgb(0, 0, 0) 0, rgba(0, 0, 0, 0) 100%);
  background: -webkit-linear-gradient(bottom, rgb(0, 0, 0) 0, rgba(0, 0, 0, 0) 100%);
  background: -o-linear-gradient(bottom, rgb(0, 0, 0) 0, rgba(0, 0, 0, 0) 100%);
  background: -ms-linear-gradient(bottom, rgb(0, 0, 0) 0, rgba(0, 0, 0, 0) 100%);
  background: linear-gradient(to top, rgb(0, 0, 0) 0, rgba(0, 0, 0, 0) 100%);
  z-index: 9;
  opacity: 0;
  filter: aplpha(opacity(0));
  -webkit-transition: all 0.12s;
  -moz-transition: all 0.12s;
  transition: all 0.12s;
}

.jFiler-no-thumbnail.jFiler-item .jFiler-item-container .jFiler-item-info {
  background: rgba(0, 0, 0, 0.55);
}

.jFiler-item .jFiler-item-container .jFiler-item-thumb:hover .jFiler-item-info {
  bottom: 0;
  opacity: 1;
  filter: aplpha(opacity(100));
}

.jFiler-item .jFiler-item-container .jFiler-item-info .jFiler-item-title {
  display: block;
  font-weight: bold;
  word-break: break-word;
  line-height: 1;
}

.jFiler-item .jFiler-item-container .jFiler-item-info .jFiler-item-others {
  display: inline-block;
  font-size: 10px;
}

.jFiler-item .jFiler-item-container .jFiler-item-assets {
  margin-top: 10px;
  color: #999;
}

.jFiler-item .jFiler-item-container .jFiler-item-assets .text-success {
  color: #3C763D;
}

.jFiler-item .jFiler-item-container .jFiler-item-assets .text-error {
  color: #A94442;
}

.jFiler-item .jFiler-item-container .jFiler-item-assets .jFiler-jProgressBar {
  width: 120px;
  margin-left: -5px;
}

.jFiler-item .jFiler-item-container .jFiler-item-assets .jFiler-item-others {
  font-size: 12px;
}

.jFiler-item-trash-action:hover {
  cursor: pointer;
  color: #d9534f;
}

