main {
  padding-top: 7rem;
  padding-bottom: 7rem;
}

.btn {
  display: block;
  width: 100%;
  min-height: 5rem;
  padding: 1.4rem 3.2rem;
  border-radius: 2rem;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 600;
  box-sizing: border-box;
}
.btn--solid {
  border: 2px solid var(--blue);
  color: var(--blue);
  background-color: transparent;
}
.btn--dashed {
  border: 2px dashed var(--blue);
  color: var(--blue);
  background-color: transparent;
}
.btn--dashed:hover {
  border: 2px dashed var(--green);
  color: var(--green);
}
.btn--blue {
  background-color: var(--blue);
  color: var(--white);
  transition: var(--great-transition);
}
.btn--blue:hover {
  background-color: var(--green);
}
.btn--blue.disabled {
  background-color: var(--border);
  cursor: auto;
}
.btn--blue.disabled:hover {
  background-color: var(--border);
}
.btn[disabled=disabled] {
  background-color: var(--border);
  cursor: auto;
}
.btn[disabled=disabled]:hover {
  background-color: var(--border);
}
.btn--lowercase {
  text-transform: lowercase;
}
.btn--with-elem {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btn--with-elem span {
  margin-right: 1rem;
}
.btn:hover {
  text-decoration: none;
}

.great-shadow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--darkgrey);
  opacity: 0;
  z-index: 10;
  transition: var(--great-transition);
  visibility: hidden;
}
.great-shadow--show {
  opacity: 0.4;
  visibility: visible;
}

.great-tab__display-elem {
  display: none;
}

main.flex-main {
  display: flex;
  flex-direction: column;
  padding-top: 0;
}
main.flex-main .inner {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.no-aplications {
  max-width: 44rem;
  text-align: center;
}
.no-aplications svg {
  display: block;
  max-width: 40.7rem;
  width: 100%;
  height: auto;
  margin: 0 auto;
}
.no-aplications .no-aplications__caption {
  display: block;
  margin-top: 3.8rem;
  font-size: 1.8rem;
  line-height: 1.36;
  color: #7482A2;
  font-weight: 600;
}
.no-aplications .btn {
  margin: 0 auto;
  min-height: 5rem;
  max-width: 20.5rem;
  margin-top: 3rem;
}

.referral-cards-box {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
  max-width: 950px;
  margin: 0 auto;
}

.referral-card {
  max-width: 46rem;
  margin: 0 auto;
  border-radius: 1rem;
  padding: 3.8rem;
  box-sizing: border-box;
  background-color: var(--white);
  box-shadow: 0px 4px 3rem -1rem rgba(142, 166, 219, 0.15);
}

.status-bar {
  background-color: var(--white);
  box-shadow: 0px 4px 30px -10px rgba(142, 166, 219, 0.15);
  padding: 0.4rem 0;
  border-radius: 1rem;
  box-sizing: border-box;
}
.status-bar .status-list li .status-list__item {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  padding: 1.4rem 2.1rem;
}
.status-bar .status-list li .status-list__item.disabled {
  cursor: auto;
}
.status-bar .status-list li .status-list__item:not(:first-child) {
  border-top: 1px solid var(--lightgrey);
}
.status-bar .status-list li .status-list__item .status-marker {
  position: relative;
  top: 0.2rem;
  margin-right: 1.4rem;
}
.status-bar .status-list li .status-list__item a, .status-bar .status-list li .status-list__item span {
  font-size: 1.4rem;
  line-height: 1.42;
  color: var(--darkgrey);
}
.status-bar .status-list li .status-list__item.current-item a, .status-bar .status-list li .status-list__item.current-item span {
  color: var(--blue);
}
.status-bar .status-list li .status-list__item:hover {
  text-decoration: none;
}

.help-information {
  background-color: var(--white);
  box-shadow: 0px 4px 30px -10px rgba(142, 166, 219, 0.15);
  padding: 2.1rem 3rem 3rem 2.1rem;
  border-radius: 1rem;
  box-sizing: border-box;
}
.help-information .help-information__caption {
  display: block;
  margin-bottom: 1rem;
  color: blue;
  font-weight: 600;
}
.help-information .help-information__status-list {
  margin-bottom: 2rem;
}
.help-information .help-information__status-list li {
  display: flex;
  align-items: flex-start;
  font-size: 1.4rem;
  line-height: 1.42;
  color: var(--darkgrey);
}
.help-information .help-information__status-list li:not(:last-child) {
  margin-bottom: 1.5rem;
}
.help-information .help-information__status-list li .status-marker {
  position: relative;
  top: 0.2rem;
  margin-right: 1rem;
}
.help-information p {
  font-size: 1.2rem;
  line-height: 1.33;
  color: var(--grey);
}

.status-marker svg {
  width: 1.6rem;
  height: 1.6rem;
}
.status-marker svg path {
  stroke: var(--red);
}
.status-marker--filled svg path {
  stroke: var(--green);
}
.status-marker--optional svg path {
  stroke: var(--grey);
}

.explanation {
  position: relative;
  background-color: var(--lightgrey);
  border-radius: 1rem;
  padding: 2rem 3rem 0 2rem;
  box-sizing: border-box;
  overflow: hidden;
}
.explanation::after {
  content: "";
  display: table;
  width: 100%;
  clear: both;
}
.explanation .explanation__icon {
  position: relative;
  top: -3px;
  float: left;
  width: 4.4rem;
  height: 4.4rem;
  margin-right: 2rem;
}
.explanation .explanation__text {
  position: relative;
  overflow: hidden;
}
.explanation .explanation__text p {
  margin-bottom: 2rem;
}
.explanation .explanation__text ul {
  margin-bottom: 2rem;
}
.explanation .explanation__text ul li {
  position: relative;
  list-style-type: none;
  padding-left: 1.1rem;
  box-sizing: border-box;
  margin-bottom: 2rem;
}
.explanation .explanation__text ul li::after {
  content: "";
  position: absolute;
  top: 0.8rem;
  left: 0;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: var(--blue);
}

.columns-box {
  display: grid;
  grid-gap: 3rem;
}
.columns-box--two-col {
  grid-template-columns: repeat(2, 1fr);
}
.columns-box--three-col {
  grid-template-columns: repeat(3, 1fr);
}
.columns-box .columns-box--col-2-3 {
