  width: 76.92%;
  flex-grow: 1;
  padding-left: 3rem;
  box-sizing: border-box;
}

@media screen and (max-width: 1090px) {
  .btn-burger {
    display: block;
    z-index: 101;
  }
  .main-menu-box {
    position: fixed;
    top: 0;
    left: -1000px;
    height: 100%;
    padding-top: 1.5rem;
    background-color: var(--white);
    z-index: 100;
    transition: var(--great-transition);
  }
  .main-menu-box--show {
    left: 0;
  }
  .main-menu-box .logo {
    display: flex;
    margin-bottom: 1.5rem;
    margin-left: 2.4rem;
  }
  .main-menu-box .main-menu {
    display: block;
  }
  .main-menu-box .main-menu li {
    border-top: 1px solid var(--border);
    border-bottom: 1px solid var(--border);
    padding: 2rem 2.4rem;
    box-sizing: border-box;
    margin-right: 0;
  }
  .main-menu-box .main-menu li a {
    padding: 0;
    font-size: 1.4rem;
    line-height: 1.2;
  }
  .main-menu-box .main-menu li a::after {
    display: none;
  }
}
@media screen and (max-width: 999px) {
  #header .header__top {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
}
@media screen and (max-width: 990px) {
  .questionnaire-section .questionnaire-box .left-col {
    display: none;
  }
  .questionnaire-section .questionnaire-box .right-col {
    width: 100%;
    padding-left: 0;
  }
}
@media screen and (max-width: 750px) {
  :root {
    font-size: 9px;
  }
  .logo img {
    margin-right: 1rem;
    margin-right: 1rem;
  }
  .output-btn {
    max-width: 21.8rem;
    padding: 1.4rem 2.2rem;
  }
}
@media screen and (max-width: 700px) {
