﻿*{box-sizing:content-box; outline:none;}
html{  
    -moz-text-size-adjust:100%;
     -ms-text-size-adjust:100%;
     -o-text-size-adjust:100%;
     -webkit-text-size-adjust:100%;
     text-size-adjust:100%;
}
:root{  
    --black: #0C111D;
    --darkgrey: #374054;
    --grey: #7482A1;
    --border: #D3DAE9;
    --lightgrey: #F2F6F8;
    --blue: #447BF8;
    --blue-hover: #164dcf;
    --lightblue: #789AEA;
    --green: #1BCCAC;
    --lightgreen: #99E9DA;
    --red: #CC1B50;
    --lightred: #FFA3BE;
    --white: #ffffff;

    --great-transition: all 200ms ease;

    font-size: 10px; 
    line-height: 1.36;
    color: var(--darkgrey); 
    font-family: 'Montserrat', sans-serif; 
    font-weight: 500;
}
 
body{ 
    position: relative;
    flex-direction: column;
    display: flex;
    background-color: #fff; 
    margin: 0 ; 
    padding:0; 
    -webkit-font-smoothing: antialiased; -moz-font-smoothing: antialiased; -o-font-smoothing: antialiased; 
    min-height:100vh;  
    background-color: var(--lightgrey);
    color: var(--darkgrey); 
    font-size: 1.4rem;
    font-weight: 500;
}
 
 
#footer{
     width: 100%;
     box-sizing: border-box;
     position: relative;
     margin-top: auto; 
}

h1, h2, h3, h4, h5, h6, p{display:block; margin:0; padding:0; font-weight:400;}
.h1, .h2, .h3, .h4, .h5, .h6{margin:0; padding:0; float:none;}
img, input, textarea, button, a{border:none; outline:none;}



input, textarea, button, a{ 
    font-family: 'Montserrat', sans-serif; 
    font-weight: 500; 
    font-size: 1.4rem; 
    line-height: 1;
}

table{border-spacing:0; border-collapse: collapse; margin: 0; padding: 0;}
td, th{vertical-align:top; word-wrap:break-word;}
ul, ol, nav, dl{margin:0; padding:0; list-style:none;}
a{text-decoration:none; font-size: inherit; line-height: inherit; color: var(--blue);  font-weight: inherit}
a:hover{text-decoration:underline;}
button{display:block; cursor:pointer; box-sizing:border-box; padding:0;}
button, .button{ text-decoration:none !important;}
//button:hover, .button:hover{transition: opacity 1.2s, visibility 0s linear 1.2s; opacity:0.7;}
address{font-style: normal;}

.inner{position:relative; max-width:1148px;  width: 96%; margin:0 auto; box-sizing: border-box; } 
section{position:relative;}
main{position:relative; clear:both; box-sizing: border-box; flex-grow: 1;}
 
header, nav, section, article, aside, footer, main{display:block;}
fieldset{position: relative; border: 0;}
legend{
    font-size: 0;
    position: absolute;
    width: 1px;
    height: 1px;
    opacity: 0;
}

.cover{background-position:center center; background-repeat:no-repeat; background-size:cover;}
.contain{background-position:center center; background-repeat:no-repeat; background-size:contain;}
 
.no-text{
    font-size: 0 !important;
    color: transparent;
    background-color: rgba(255, 255, 255, 0.504);
}

//.clear{clear:both; height:1px;}
.fleft{float:left;}
.fright{float:right;}
.db{display:block;}
.inb{display:inline-block;}
.in{display:inline;}
.tb{display:table;}
.tbc{display:table-cell;}
.tbr{display:table-row-group;}
.tr{display:table-row;}
.on_dekstop{display:block;}
.on_mobile{display:none;}
.hide{display:none;}
