.clearfix::after, .clear::after{content: ''; display: table; width: 100%; clear: both;}


.placeholder::-webkit-input-placeholder{opacity: 1; transition: opacity 0.3s ease; color: var(--grey);}
.placeholder::-moz-placeholder{opacity: 1; transition: opacity 0.3s ease; color:var(--grey);}
.placeholder:-moz-placeholder{opacity: 1; transition: opacity 0.3s ease; color:var(--grey);}
.placeholder:-ms-input-placeholder{opacity: 1; transition: opacity 0.3s ease; color:var(--grey);}
.placeholder:focus::-webkit-input-placeholder{opacity: 0; transition: opacity 0.3s ease;}
.placeholder:focus::-moz-placeholder{opacity: 0; transition: opacity 0.3s ease;}
.placeholder:focus:-moz-placeholder{opacity: 0; transition: opacity 0.3s ease;}
.placeholder:focus:-ms-input-placeholder{opacity: 0; transition: opacity 0.3s ease;}

.textarea::-webkit-input-placeholder{opacity: 1; transition: opacity 0.3s ease;}
.textarea::-moz-placeholder{opacity: 1; transition: opacity 0.3s ease;}
